kground-color) 50%, black) 2px 2px 6px;\n        }\n\n        &:after {\n            content: '';\n            position: absolute;\n            top: 0;\n            right: 100%;\n            height: 100%;\n            padding: 0 1.5ch;\n            z-index: -1;\n        }\n    }\n\n    li {\n        margin: 0;\n        padding: 0 1.5ch;\n    }\n\n    a,\n    a:visited {\n        margin: 0 0.5ch;\n        color: var(--accent-1);\n    }\n\n    button:active {\n        scale: var(--hover-scale);\n    }\n}\n"],"sourceRoot":""}]);
